.storage-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.storage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background-color: #d9d9d9;*/
    width: 374px;
    height: 728px;
    position: relative;
}
.storage-background-img{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 374px;
    height: 728px;
    position: absolute;
    z-index: 0;
}
.storage-components{
    position: relative;
    z-index: 2;
    border: 1px solid #d9d9d9;
    border-radius: 15px;
    background-color: white;
    width: 354px;
    height: 708px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0.95;
}
.storage-select-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-left: 190px;
    margin-bottom: 670px;
}
.storage-selects{
    display: flex;
}
.form-select{
    margin-right: 10px;
    border: 1px solid #d9d9d9;
    border-radius: 7px;
    padding: 3px;
}
.order-select{
    margin-right: 10px;
    border: 1px solid #d9d9d9;
    border-radius: 7px;
    padding: 3px;
}
.storage-menu-group{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}
.menu-group-label{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 32px;
    font-size: 11px;
    width: 15%;
    margin-left: 32px;
}
.storage-menu-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.menu-btn{
    border: none;
    border-radius: 7px;
    background-color: lightsalmon;
    color: white;
    width: 330px;
    height: 36px;
    font-size: 14px;
    margin-bottom: 7px;
    cursor: pointer;
}
.menu-btn:hover{
    border: 2px solid lightsalmon;
    background-color: white;
    color: lightsalmon;
}
.menu-btn:active{
    border: none;
    background-color: lightsalmon;
    color: white;
}