.url-input-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.url-input{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.url-input-text{
    font-size: 15px;
}
.main-url-input{
    width: 275px;
    height: 35px;
    border-radius: 15px;
    border: none;
    background-color: antiquewhite;
    padding-left: 15px;
    margin-bottom: 20px;
    color: black;;
}
.main-url-input:focus{
    outline: 2px solid lightsalmon;
}
.url-input-save-button{
    border: none;
    background-color: lightsalmon;
    border-radius: 7px;
    width: 60px;
    height: 21px;
    font-size: 13px;
    color: white;
    cursor: pointer;
}
.url-input-save-button:hover{
    background-color: white;
    border: 1px solid lightsalmon;
    color: lightsalmon;
}
.url-input-save-button:active{
    background-color: lightsalmon;
    color: white;
}