.login-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background-color: #d9d9d9;*/
    width: 374px;
    height: 776px;
    position: relative;
}
.login-background-img{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 374px;
    height: 776px;
    position: absolute;
    z-index: 0;
}
.login-components{
    position: relative;
    z-index: 2;
    border: 1px solid #d9d9d9;
    border-radius: 15px;
    background-color: white;
    width: 355px;
    height: 225px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0.95;
}
.login-title{
    font-size: 20px;
}
.login-btn{
    background-color: midnightblue;
    border: none;
    width: 195px;
    height: 30px;
    border-radius: 7px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    color: white;
}
.login-btn:hover{
    border: 2px solid midnightblue;
    background-color: white;
    color: midnightblue;
}
.login-btn:active{
    border: none;
    background-color: midnightblue;
    color: white;
}
.login-logo{
    width: 25px;
    height: 25px;
    margin-right: 115px;
    border-radius: 100px;
    position: absolute;
}
.login-text{
    position: absolute;
    margin-left: 35px;
}
.logout-picture{
    cursor: auto;
    height: 56px;
    display: flex;
    margin-top: -10px;
}
.logout-btn{
    background-color: lightsalmon;
    color: white;
    border: none;
    width: 195px;
    height: 30px;
    border-radius: 7px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.logout-btn:hover{
    border: 2px solid lightsalmon;
    background-color: white;
    color: lightsalmon;
}
.logout-btn:active{
    border: none;
    background-color: lightsalmon;
    color: white;
}