.create-storage-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.create-storage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.create-storage-text{
    font-size: 15px;
    margin-bottom: 20px;
}
.create-storage-input{
    width: 275px;
    height: 35px;
    border-radius: 15px;
    border: none;
    background-color: antiquewhite;
    color: black;;
    font-size: 17px;
    text-align: center;
}
.create-storage-input:focus{
    outline: 2px solid lightsalmon;
}