.detaile-storage-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.detaile-storage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background-color: #d9d9d9;*/
    width: 374px;
    height: 728px;
    position: relative;
}
.detaile-storage-background-img{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 374px;
    height: 728px;
    position: absolute;
    z-index: 0;
}
.detaile-storage-components{
    position: relative;
    z-index: 2;
    border: 1px solid #d9d9d9;
    border-radius: 15px;
    background-color: white;
    width: 354px;
    height: 708px;
    display: flex;
    flex-direction: column;
    opacity: 0.95;
}
.detaile-select-container{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 20px;
    width: 354px;
}
.detaile-select{
    display: flex;
    margin-top: 20px;
    margin-right: 20px;
}
.objects{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-row-gap: 6px;
    place-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.obj{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 32.5%;
    margin-bottom: 2px;
}
.obj > img {
    width: 110px;
    height: 100px;
    display: flex;
}
.clicked {
    transform: scale(1.2);
    transition: transform 0.5s;
    transition-timing-function: ease;
    display: flex;
    flex-direction: row;
    border: none;
    flex-wrap: nowrap;
    grid-column: 1/4;
    width: 230px;
    margin-bottom: 8px;
    margin-top: 6px;
}
.clicked-img {
    width: 100%;
}
.clicked-info{
    display: flex;
    float: right;
    flex-direction: column;
    justify-content: center;
    color: black;
    font-size: 10px;
    margin-left: 5px;
    word-wrap: break-word; /* 텍스트가 단어 단위로 줄 바꿈되도록 설정 */
    overflow-wrap: break-word; /* Firefox와 Chrome에서 줄 바꿈되도록 설정 */
    overflow-x: hidden; /* 가로 스크롤이 생성되지 않도록 설정 */
}
.clicked-info > p {
    margin: 0;
}
