.storage-list-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.storage-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 355px;
    height: 357px;
    background-color: white;
    border: 1px solid #d9d9d9;
    border-radius: 15px;
}
.list-btns{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.storage-list-btn{
    width: 290px;
    height: 41px;
    border-radius: 7px;
    border: none;
    background-color: lightsalmon;
    color: white;
    margin-bottom: 5px;
    font-size: 16px;
}
.storage-list-btn:hover{
    border: 2px solid lightsalmon;
    background-color: white;
    color: lightsalmon;
    cursor: pointer;
}
.storage-list-btn:active{
    border: none;
    background-color: lightsalmon;
    color: white;
}
.clicked{
    border: 2px solid lightsalmon;
    background-color: white;
    color: lightsalmon;
}
.next-btns{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}
.pre-btn{
    width: 36px;
    height: 30px;
    border-radius: 7px;
    border: 2px solid lightsalmon;
    background-color: white;
    color: lightsalmon;
    margin-bottom: 5px;
    font-size: 11px;
    margin-right: 60px;
    cursor: pointer;
}
.pre-btn:active{
    border: none;
    background-color: lightsalmon;
    color: white;
}
.next-btn{
    width: 36px;
    height: 30px;
    border-radius: 7px;
    border: 2px solid lightsalmon;
    background-color: white;
    color: lightsalmon;
    margin-bottom: 5px;
    font-size: 11px;
    cursor: pointer;
}
.next-btn:active{
    border: none;
    background-color: lightsalmon;
    color: white;
}