.menubar-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.menubar{
    border: 1px solid #d9d9d9;
    width: 375px;
    height: 55px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.repository-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 55px;
    width: 55px;
    height: 55px;
    border: none;
    background-color: white;
    border-radius: 10px;
}
.repository-icon:hover{
    border: 3px solid lightsalmon;
}
.repository-icon:active{
    background-color: lightsalmon;
    border: none;
}
