.main-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background-color: #d9d9d9;*/
    width: 374px;
    height: 728px;
    position: relative;
}
.main-background-img{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 374px;
    height: 728px;
    position: absolute;
    z-index: 0;
}
.main-create{
    position: relative;
    z-index: 2;
    border: 1px solid #d9d9d9;
    border-radius: 15px;
    background-color: white;
    width: 355px;
    height: 225px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0.95;
}